<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img :src="seller.avatar" width="64" height="64" />
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description + ' / ' + seller.deliveryTime + '分钟送达'}}
        </div>
        <div v-if="seller.supports" class="support">
          <span class="icon" :class="iconClassMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div v-if='seller.supports'class="support-count" @click="showDetails()">
        <span class="count">{{seller.supports.length }}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div class="bulletin-wrapper" @click="showDetails()">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" width="100%" height="100%">
    </div>
    <transition name="fade">
    <!--浮层 CSS Sticky Footer解决底部-->
    <div class="detail" v-show="detailShow">
      <!--外层要清除浮动-->
      <div class="detail-wrapper clearfix">
        <!--内容-->
        <div class="detail-main">
          <h1 class="name">{{seller.name}}</h1>
          <div class="star-wrapper">
            <star :size="48" :score="seller.score"></star>
          </div>
          <div class="title">
            <!--用span的话，某些安卓机器会出现样式上的改变-->
            <div class="line"></div>
            <div class="text">优惠信息</div>
            <div class="line"></div>
          </div>
          <ul class="supports" v-if="seller.supports">
            <li class="support-item" v-for="item in seller.supports">
              <span class="icon" :class="iconClassMap[item.type]"></span>
              <span class="text">{{item.description}}</span>
            </li>
          </ul>
          <div class="title">
            <!--用span的话，某些安卓机器会出现样式上的改变-->
            <div class="line"></div>
            <div class="text">商家公告</div>
            <div class="line"></div>
          </div>
          <div class="bulletin">
            <p class="content">{{seller.bulletin}}</p>
          </div>
        </div>
      </div>
      <!--底部层-->
      <div class="detail-close">
        <i class="icon-close" @click="hideDetail()"></i>
      </div>
    </div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
  import star from 'components/star/star';
  export default{
    props: {
          seller: {
              type: Object
          }
    },
    data () {
        return {
            detailShow:false
        };
    },
    created() {
      this.iconClassMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
    },
    methods: {
      showDetails() {
        this.detailShow = true;
      },
      hideDetail() {
        this.detailShow = false;
      }
    },
    components: {
      star
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .header
     color #fff
     position relative
     overflow hidden
     background rgba(7,17,27,0.5)
     .content-wrapper
       position relative
       padding 24px 12px 18px 24px
       /*消除空白*/
       font-size 0
       .avatar
         vertical-align top
         display inline-block
         img
           border-radius 2px
       .content
         display inline-block
         margin-left 16px
         font-size 14px
         .title
           margin 2px 0 8px 0
           .brand
             display inline-block
             /*对齐方式为顶部对齐*/
             vertical-align top
             width: 30px
             height: 18px
             bg-image('brand')
             background-size 30px 18px
             background-repeat no-repeat
           .name
             margin-left 6px
             font-size 16px
             line-height 18px
         .description
           margin-bottom 10px
           line-height 12px
           font-size 12px
         .support
           .icon
             display inline-block
             width 12px
             height 12px
             margin-right 4px
             background-size 12px 12px
             background-repeat no-repeat
             &.decrease
               bg-image('decrease_1')
             &.discount
               bg-image('discount_1')
             &.guarantee
               bg-image('guarantee_1')
             &.invoice
               bg-image('invoice_1')
             &.special
               bg-image('special_1')
           .text
             line-height 12px
             vertical-align top
             font-size 10px
       .support-count
           position absolute
           right 12px
           bottom 18px
           padding 0 8px
           height 24px
           line-height 24px
           border-radius 14px
           background-color rgba(0,0,0,0.2)
           text-align center
         .count
           vertical-align top
           font-size 10px
         .icon-keyboard_arrow_right
           font-size 10px
           margin-left 2px
           line-height 24px
     .bulletin-wrapper
       position relative
       height 28px
       line-height 28px
       padding 0 22px 0 12px
       white-space nowrap
       overflow hidden
       text-overflow ellipsis
       background rgba(7,17,27,0.2)
       .bulletin-title
         display inline-block
         vertical-align top
         margin-top 8px
         width: 22px
         height: 12px
         bg-image('bulletin')
         background-size 22px 12px
         background-repeat no-repeat
       .bulletin-text
         margin 0 4px
         vertical-align top
         font-size 10px
       .icon-keyboard_arrow_right
         position absolute
         font-size 10px
         right 12px
         top 8px

     .background
       position absolute
       top 0
       left 0
       width 100%
       height 100%
       z-index -1
       /*css3滤镜模糊效果*/
       filter blur(10px)
     .detail
       position fixed
       z-index 100
       top 0
       left 0
       width 100%
       height 100%
       overflow auto
       background rgba(7,17,27,0.8)
       backdrop-filter blur(10px)
       .detail-wrapper
         /*给最小高度是为了保证能把底部撑下去*/
         min-height 100%
         width 100%
         .detail-main
           margin-top 64px
           /*套路只能用padding*/
           padding-bottom 64px
           .name
             line-height 16px
             text-align center
             font-size 16px
             font-weight 700
           .star-wrapper
             margin-top 18px
             padding 2px 0
             text-align center
           .title
             display flex
             width 80%
             margin 28px auto 24px auto
             .line
               flex 1
               position relative
               top -6px
               border-bottom 1px solid rgba(255,255,255,0.2)
             .text
               padding 0 12px
               font-size 14px
               font-weight 700
           .supports
             width 80%
             margin 0 auto
             .support-item
               padding 0 12px
               margin-bottom 12px
               font-size 0
               &:last-child
                 margin-bottom 0
               .icon
                 display inline-block
                 width 16px
                 height 16px
                 vertical-align top
                 margin-right 16px
                 background-size 16px 16px
                 background-repeat no-repeat
                 &.decrease
                   bg-image('decrease_2')
                 &.discount
                   bg-image('discount_2')
                 &.guarantee
                   bg-image('guarantee_2')
                 &.invoice
                   bg-image('invoice_2')
                 &.special
                  bg-image('special_2')
               .text
                 line-height 16px
                 font-size 12px
           .bulletin
             width 80%
             margin 0 auto
             .content
               padding 0 12px
               line-height 24px
               font-size 12px
       .detail-close
         position relative
         width 32px
         height 32px
         margin -64px auto 0 auto
         clear both
         font-size 32px
       /*点击时候效果*/
       &.fade-enter-active, &.fade-leave-active {
         transition: opacity .5s
       }
       /*点击和消失的时候效果*/
       &.fade-enter, &.fade-leave-active {
         opacity: 0
        }

</style>



